export const getUserData = async (userId) => {
    try {
      // –î–æ–±–∞–≤–ª—è–µ–º `userId` –≤ URL –∑–∞–ø—Ä–æ—Å–∞
      const res = await fetch(`https://bonusnumber1.com/api/user/read_one.php?id=${userId}`);
  
      if (res.ok) {
        const responseData = await res.json();
     
        return responseData; // –í–æ–∑–≤—Ä–∞—â–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
      } else {
        console.error("Failed to fetch data:", res.status);
        return null; // –í–æ–∑–≤—Ä–∞—â–∞–µ–º null –≤ —Å–ª—É—á–∞–µ –æ—à–∏–±–∫–∏
      }
    } catch (error) {
      console.error("An error occurred:", error);
      return null; // –í–æ–∑–≤—Ä–∞—â–∞–µ–º null –≤ —Å–ª—É—á–∞–µ –æ—à–∏–±–∫–∏
    }
  };
  